body
{
  background-color: black;
  color: #fff;
}
*{
  margin:0;padding:0;box-sizing: border-box;
}
li{
  list-style: none;
}
.img img{
  width: 30rem;
  height: 20rem;
  border-radius: 10px;
}

#about-btn
{
  height: 20rem;
}
#darkmode
{
  cursor: pointer;
}
.main-home
{
  position: absolute;top: 0%;
  left: 0;
  width: 100%;
  height: 100vh;
  background:linear-gradient(rgba(000,000,000,0.5),rgba(000,000,000,0.3)), url(components/images/home.jpg);
  background-position: center;
  background-size: cover;
}